@import url(https://fonts.googleapis.com/css?family=Roboto:400,700|Staatliches&display=swap);
*{margin:0;padding:0;font-family:Roboto, sans-serif;outline:none}*::selection{color:#ED1E24;background-color:white}a{text-decoration:none;color:unset}body{overflow-y:scroll}


.text-input-container{background-color:white;border-radius:1000px;display:flex;align-items:center}.text-input-container .text-input{border:none;background-color:transparent;padding:.5rem 1.5rem;font-size:calc(.3vw + .7rem);max-width:20vw}.text-input-container .text-input::selection{background-color:#ED1E24;color:white}

.search-input-button{background-color:transparent;border:none;margin:2px;display:flex;justify-content:center;align-items:center;border-radius:50%;padding:calc(.5vw + .5rem);-webkit-transition:background-color .2s;transition:background-color .2s;cursor:pointer}.search-input-button:hover{background-color:rgba(237,30,36,0.15)}.search-input-button:active{background-color:rgba(237,30,36,0.3)}.search-input-button img{width:1.25rem;height:1.25rem}

.pokedex-layout{background-color:#ED1E24;color:white;overflow-x:hidden;position:relative;min-height:100vh}.pokedex-layout nav{position:relative;padding:16px;display:flex;align-items:center;justify-content:space-between}.pokedex-layout nav .pokemon-buttons{display:grid;grid-auto-flow:column}.pokedex-layout nav .pokemon-buttons .blue-sensor{width:calc(3vw + 3rem);margin-right:2vw}.pokedex-layout nav .pokemon-buttons img:not(.blue-sensor){width:calc(1vw + 1rem);margin-right:calc(.5vw + .25rem)}.pokedex-layout nav .pokedex-title{align-self:center}@media screen and (max-width: 768px){.pokedex-layout nav .pokedex-title{display:none}}.pokedex-layout nav .pokedex-title h1{font-size:calc(2vw + .5rem);font-weight:lighter}.pokedex-layout nav .pokedex-title h1 span{font-size:calc(2vw + .75rem);font-family:Staatliches, Segoe UI, sans-serif}.pokedex-layout .background-decoration img{position:relative}.pokedex-layout .background-decoration img{width:calc(100vw + 64px);-webkit-transform:translateX(-32px) translateY(-1vw);transform:translateX(-32px) translateY(-1vw)}@media screen and (max-width: 768px){.pokedex-layout .background-decoration img{width:calc(150vw + 64px)}}.pokedex-layout .background-pokeball{opacity:.1;position:absolute;right:-1vw;top:-1vw}.pokedex-layout .background-pokeball img{width:40vw;min-width:320px;max-width:960px}@media screen and (max-width: 768px){.pokedex-layout .background-pokeball img{display:none}}.pokedex-layout .children-wrapper{position:relative}

.page-status{display:grid;grid-auto-flow:column;grid-gap:calc(1vw + 1rem);justify-content:start}.page-status.blue .text-area{background-color:#3476CC}.page-status.green .text-area{background-color:#43CC34}.page-status.yellow .text-area{background-color:#F5CC04;color:black}.page-status .left-marker{height:calc(2vw + 2rem)}.page-status .text-area{border:calc(.075vw + .1rem) solid black;border-radius:8px;height:100%;padding:0 calc(1vw + .5rem);display:flex;align-items:center;background-color:#3476CC}.page-status .text-area p{font-family:Staatliches, sans-serif;font-size:calc(1vw + 1rem);min-width:30vw}

.page-details{padding:calc(1vw + 1rem);display:grid;grid-gap:calc(1vw + 1rem);grid-auto-flow:row;justify-content:stretch}.page-details .page-status-container{display:grid;grid-auto-flow:column;justify-content:start;justify-items:start;grid-gap:calc(.5vw + .5rem)}@media screen and (max-width: 768px){.page-details .page-status-container{grid-auto-flow:row;grid-template-columns:repeat(auto-fit, 100%)}.page-details .page-status-container>*:not(:first-child){justify-self:center}}.page-details .pokemon-details{display:grid;grid-template-columns:calc(15vw + 15rem) 1fr;grid-gap:calc(1vw + 1rem);align-content:start;justify-content:center}@media screen and (max-width: 768px){.page-details .pokemon-details{grid-template-columns:repeat(auto-fit, calc(15vw + 15rem))}}.page-details .pokemon-details .pokemon-image{background-color:white;border:3px solid black;display:flex;max-width:calc(15vw + 15rem);max-height:calc(15vw + 15rem);overflow:hidden}.page-details .pokemon-details .pokemon-image img{min-width:calc(100% + 3rem);margin-left:-1.5rem;object-fit:cover}.page-details .pokemon-details .pokemon-details-list{display:grid;align-content:start;justify-content:start;grid-row-gap:calc(.5vw + .5rem);grid-column-gap:calc(2vw + 2rem)}@media screen and (max-width: 768px){.page-details .pokemon-details .pokemon-details-list{grid-template-columns:repeat(2, 1fr)}}.page-details .pokemon-details .pokemon-details-list div span{opacity:.75;font-size:calc(1vw + .5rem)}.page-details .pokemon-details .pokemon-details-list div h2,.page-details .pokemon-details .pokemon-details-list div p{font-family:Staatliches, sans-serif}.page-details .pokemon-details .pokemon-details-list div h2{font-size:calc(2vw + 2rem);font-weight:lighter}.page-details .pokemon-details .pokemon-details-list div p{font-size:calc(1vw + 1rem)}.page-details .pokemon-details .pokemon-details-list .pokemon-types,.page-details .pokemon-details .pokemon-details-list .pokemon-name{grid-column-end:span 2}.page-details .pokemon-details .pokemon-details-list .pokemon-types{display:grid;grid-auto-flow:row;grid-gap:calc(.25vw + .25rem)}.page-details .pokemon-details .pokemon-details-list .pokemon-types .pokemon-types-list{display:grid;grid-auto-flow:column;grid-gap:calc(.5vw + .5rem);justify-content:start}

.inflated-button{border:calc(.075vw + .1rem) solid black;border-radius:9999px;height:100%;padding:0 calc(1vw + 1rem);display:flex;align-items:center;position:relative;background-color:lightgrey;overflow:hidden;cursor:pointer;min-height:calc(1.5vw + 1.5rem)}.inflated-button:active .background-decoration{-webkit-transform:translateY(0);transform:translateY(0)}.inflated-button .background-decoration{position:absolute;width:100%;height:100%;background-color:white;border-radius:9999px;left:0;top:0;-webkit-transform:translateY(calc((.25vw + .25rem) * -1));transform:translateY(calc((.25vw + .25rem) * -1));-webkit-transition:-webkit-transform .2s;transition:-webkit-transform .2s;transition:transform .2s;transition:transform .2s, -webkit-transform .2s}.inflated-button>*:not(.background-decoration){position:relative;color:black;font-size:calc(1vw + .5rem)}

.pokemon-type-badge{display:flex;align-items:center;background-color:white;border-radius:9999px;padding:calc(.25vw + .5rem) calc(.25vw + .75rem);color:black}.pokemon-type-badge.small p{font-size:calc(.25vw + 1rem) !important}.pokemon-type-badge p{font-family:Roboto, sans-serif !important;font-size:calc(1vw + 1rem) !important;text-align:center;margin-right:calc(.25vw + .25rem)}.pokemon-type-badge svg{width:calc(.5vw + 1rem);height:auto}.pokemon-type-badge.bug{color:#ccb54f}.pokemon-type-badge.bug svg{fill:#ccb54f}.pokemon-type-badge.dark{color:#000}.pokemon-type-badge.dark svg{fill:#000}.pokemon-type-badge.dragon{color:#db0000}.pokemon-type-badge.dragon svg{fill:#db0000}.pokemon-type-badge.electric{color:#ffa80a}.pokemon-type-badge.electric svg{fill:#ffa80a}.pokemon-type-badge.fairy{color:#d1b64c}.pokemon-type-badge.fairy svg{fill:#d1b64c}.pokemon-type-badge.fighting{color:red}.pokemon-type-badge.fighting svg{fill:red}.pokemon-type-badge.fire{color:#ff7e00}.pokemon-type-badge.fire svg{fill:#ff7e00}.pokemon-type-badge.flying{color:#009dff}.pokemon-type-badge.flying svg{fill:#009dff}.pokemon-type-badge.ghost{color:#6e77a8}.pokemon-type-badge.ghost svg{fill:#6e77a8}.pokemon-type-badge.grass{color:#4cb161}.pokemon-type-badge.grass svg{fill:#4cb161}.pokemon-type-badge.ground{color:#803928}.pokemon-type-badge.ground svg{fill:#803928}.pokemon-type-badge.ice{color:#527fa3}.pokemon-type-badge.ice svg{fill:#527fa3}.pokemon-type-badge.normal{color:#5e5e5e}.pokemon-type-badge.normal svg{fill:#5e5e5e}.pokemon-type-badge.poison{color:#ff3844}.pokemon-type-badge.poison svg{fill:#ff3844}.pokemon-type-badge.psychic{color:#7d4cff}.pokemon-type-badge.psychic svg{fill:#7d4cff}.pokemon-type-badge.rock{color:#999}.pokemon-type-badge.rock svg{fill:#999}.pokemon-type-badge.steel{color:#616161}.pokemon-type-badge.steel svg{fill:#616161}.pokemon-type-badge.water{color:#007aff}.pokemon-type-badge.water svg{fill:#007aff}

.page-list{padding:calc(1vw + 1rem);display:grid;grid-gap:calc(1vw + 1rem);grid-auto-flow:row;justify-content:stretch}.page-list .page-status-container{display:grid;grid-auto-flow:column;justify-content:start;justify-items:start;grid-gap:calc(.5vw + .5rem)}@media screen and (max-width: 768px){.page-list .page-status-container{grid-auto-flow:row;grid-template-columns:repeat(auto-fit, 100%)}.page-list .page-status-container>*:not(:first-child){justify-self:center}}.page-list .pokemons-list{display:grid;grid-template-columns:repeat(auto-fit, calc(8vw + 16rem));justify-content:center;grid-gap:calc(16px);list-style:none}.page-list .pokemons-list li{border:3px solid black;background-color:white;border-radius:2px;overflow:hidden;position:relative}.page-list .pokemons-list li:hover .pokemon-image{-webkit-transform:scale(1.1);transform:scale(1.1)}.page-list .pokemons-list li .pokemon-image{width:calc(8vw + 20rem);height:calc(8vw + 20rem);-webkit-transition:-webkit-transform .2s;transition:-webkit-transform .2s;transition:transform .2s;transition:transform .2s, -webkit-transform .2s;display:flex}.page-list .pokemons-list li .pokemon-data{position:absolute;left:0;bottom:24px;border:3px solid black;border-left:none;background-color:rgba(237,30,36,0.8);padding:calc(.5vw + .5rem)}.page-list .pokemons-list li .pokemon-data .pokemon-name{display:grid;grid-auto-flow:column;align-items:center;justify-content:start;grid-gap:8px}.page-list .pokemons-list li .pokemon-data .pokemon-name p{font-family:Staatliches, sans-serif;font-size:calc(1vw + 1.5rem)}.page-list .pokemons-list li .pokemon-data .pokemon-name span{font-family:Consolas, Roboto, sans-serif;font-size:calc(.5vw + .5rem);opacity:.75}.page-list .pokemons-list li .pokemon-data .pokemon-types{display:grid;grid-gap:8px;grid-auto-flow:column;justify-content:start}

